/* App.css â€” responsive + glass + nav fix */

/* Base variables */
:root{
  --bg-1:#071026;
  --bg-2:#0f1724;
  --glass: rgba(255,255,255,0.04);
  --accent1:#7c3aed;
  --accent2:#6366f1;
  --muted:#9aa8c7;
}

/* Reset & base */
*{box-sizing:border-box}
html{scroll-behavior:smooth}
body{
  margin:0;
  font-family:"Poppins", system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial;
  background:
    radial-gradient(circle at 10% 10%, rgba(124,58,237,0.06), transparent 15%),
    linear-gradient(180deg,var(--bg-1),var(--bg-2));
  color:#e6eef8;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale;
  min-height:100vh;
}

/* Container */
.container{max-width:1100px;margin:10px auto;padding:0 18px 60px;}
.section{margin:28px 0}

/* NAV */
.nav {
  position: fixed;
  top: 12px;
  left: 50%;
  transform: translateX(-50%);   /* keeps it centered even when fixed */
  z-index: 9999;

  max-width: 1100px;
  width: 100%;
  margin: 10px auto;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 10px 18px;

  backdrop-filter: blur(8px) saturate(120%);
  background: linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border-radius: 12px;
  border: 1px solid rgba(255,255,255,0.03);
  box-shadow: 0 6px 30px rgba(2,6,23,0.6);
}


/* brand */
.brand{font-weight:700;color:var(--accent1);letter-spacing:-0.5px}

/* Desktop nav links */
.nav-links{display:flex;gap:10px}
.nav-links button{
  background:transparent;border:none;color:var(--muted);
  padding:8px 10px;border-radius:8px;font-weight:600;cursor:pointer;
  transition:all .18s ease;
}
.nav-links button:hover{color:#eaf2ff;transform:translateY(-2px)}
.nav-links button.active{
  background:linear-gradient(90deg,var(--accent2),var(--accent1));
  color:#fff;box-shadow:0 10px 30px rgba(99,102,241,0.12);
}

/* Mobile toggle */
.nav-toggle{
  display:none;
  background:transparent;border:none;padding:8px;width:44px;height:40px;
  align-items:center;justify-content:center;cursor:pointer;
}
.nav-toggle span{
  display:block;width:20px;height:2px;background:#dbeafe;margin:4px 0;border-radius:2px;
  transition: all .18s ease;
}
.nav-toggle.open span:nth-child(1){transform:translateY(6px) rotate(45deg)}
.nav-toggle.open span:nth-child(2){opacity:0}
.nav-toggle.open span:nth-child(3){transform:translateY(-6px) rotate(-45deg)}

/* HERO */
.hero{text-align:center;padding:48px 20px 40px;max-width:1100px;margin:16px auto}
.hero h1{font-size:3rem;margin:6px 0 4px;color:var(--accent1);text-shadow:0 6px 25px rgba(124,58,237,0.12)}
.hero h2{font-size:1.05rem;color:var(--muted);margin:0 0 18px}
.hero-cta{display:flex;gap:12px;justify-content:center;margin-top:18px}

/* Buttons */
.btn-primary{
  background:linear-gradient(90deg,var(--accent2),var(--accent1));
  color:white;padding:10px 16px;border-radius:12px;font-weight:700;text-decoration:none;
  box-shadow:0 10px 30px rgba(99,102,241,0.12);
}
.btn-primary:hover{transform:translateY(-4px);box-shadow:0 18px 50px rgba(99,102,241,0.18)}
.btn-secondary{
  background:transparent;color:#dbeafe;border:1px solid rgba(255,255,255,0.04);
  padding:9px 14px;border-radius:10px;font-weight:700;
}

/* Glass card base */
.glass{
  background:linear-gradient(180deg, rgba(255,255,255,0.02), rgba(255,255,255,0.01));
  border:1px solid rgba(255,255,255,0.035);
  border-radius:14px;padding:18px;backdrop-filter:blur(8px);
  transition:transform .22s ease, box-shadow .22s ease, background .22s ease;
}
.glass:hover{transform:translateY(-6px);box-shadow:0 18px 50px rgba(99,102,241,0.12)}

/* Skills */
.skills-grid{display:grid;grid-template-columns:repeat(auto-fit,minmax(140px,1fr));gap:12px}
.skill-card{display:flex;gap:12px;align-items:center;justify-content:flex-start;padding:14px}
.skill-icon{width:44px;height:44px;border-radius:10px;background:linear-gradient(90deg,var(--accent2),var(--accent1));display:flex;align-items:center;justify-content:center;color:white;font-weight:700;box-shadow:0 8px 26px rgba(99,102,241,0.12)}
.skill-name{font-weight:700;color:#eaf2ff}

/* Projects */
.projects-grid{display:grid;grid-template-columns:repeat(auto-fit,minmax(240px,1fr));gap:14px}
.project-card{padding:16px;border-radius:12px;display:flex;flex-direction:column}
.project-thumb{height:140px;background-size:cover;background-position:center;border-radius:10px;margin-bottom:12px}
.project-body h4{margin:0 0 6px}
.tag-row{display:flex;gap:8px;flex-wrap:wrap;margin-top:10px}
.tag{background:rgba(255,255,255,0.03);padding:6px 10px;border-radius:8px;font-weight:700;font-size:.9rem;color:var(--muted)}

/* experience / education */
.experience-card,.education-card{max-width:900px;margin:8px auto;padding:16px;border-radius:12px}
.row-between{display:flex;justify-content:space-between;align-items:center;gap:10px}
.muted{color:var(--muted);font-size:.95rem}
.short-list{margin:10px 0 0 18px;color:#dbeafe}

/* resume */
.resume-section .resume-wrap{display:flex;gap:16px;align-items:stretch;flex-wrap:wrap}
.resume-container{width:100%;min-height:680px;border-radius:12px;overflow:hidden}
.resume-container object{width:100%;height:100%;min-height:680px;border:none}

/* resume actions */
.resume-download{margin-top:14px}
.btn-primary.large{padding:12px 18px;font-size:.98rem}

/* contact */
.contact-cards{display:flex;gap:12px;flex-wrap:wrap;margin-top:12px}
.contact-card{min-width:170px;padding:12px;border-radius:12px;text-align:left}

/* modal */
.modal-backdrop{
  position:fixed;inset:0;background:rgba(3,6,22,0.6);display:flex;align-items:center;justify-content:center;z-index:120;
}
.modal-card{width:92%;max-width:980px;padding:18px;border-radius:14px;position:relative}
.modal-close{
  position:absolute;right:14px;top:12px;background:transparent;border:none;font-size:26px;cursor:pointer;
  width:44px;height:44px;border-radius:8px;display:flex;align-items:center;justify-content:center;
  box-shadow:0 6px 20px rgba(2,6,23,0.45);
}
.modal-grid{display:grid;grid-template-columns:420px 1fr;gap:16px;align-items:start}
.modal-image-wrap{min-height:240px}
.modal-image{width:100%;height:100%;object-fit:cover;border-radius:10px}
.modal-body h3{margin:0}

/* footer */
.footer{text-align:center;color:var(--muted);padding:28px 0 60px}

/* responsive rules */
@media (max-width: 980px){
  .modal-grid{grid-template-columns:1fr}
  .project-thumb{height:160px}
  .hero h1{font-size:2.6rem}
}

/* mobile nav */
@media (max-width: 820px){
  .nav{padding:10px 14px;margin:8px}
  .nav-links{position:fixed;right:12px;top:72px;background:linear-gradient(180deg, rgba(2,6,23,0.85), rgba(2,6,23,0.95));backdrop-filter:blur(8px);padding:12px;border-radius:12px;border:1px solid rgba(255,255,255,0.03);flex-direction:column;gap:8px;display:none;min-width:160px;box-shadow:0 10px 40px rgba(2,6,23,0.6)}
  .nav-links.open{display:flex}
  .nav-toggle{display:flex}
  .nav-links button{width:100%;text-align:left;padding:10px 12px}
}

/* small phones */
@media (max-width: 420px){
  .hero h1{font-size:2rem}
  .project-thumb{height:120px}
  .skills-grid{grid-template-columns:repeat(auto-fit,minmax(120px,1fr))}
  .modal-card{padding:12px}
}
