/* --- OVERLAY --- */
.project-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  opacity: 0;
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: opacity 0.5s ease, visibility 0.5s ease;
  border-radius: 12px;
}

.project-thumb:hover .project-overlay {
  opacity: 1;
  visibility: visible;
}

.overlay-content {
  text-align: center;
  color: #fff;
}

.overlay-content h4 {
  margin-bottom: 10px;
  font-size: 1.1rem;
  font-weight: 600;
  letter-spacing: 0.5px;
