/* FloatingLines demo layout & color tuning (paste at bottom of App.css) */

html, body, #root {
  height: 100%;
  margin: 0;
  padding: 0;
}

.landing-root {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  background: transparent !important;
}


/* canvas container (floating lines) - behind UI */
.floating-lines-container {
  position: fixed !important;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: -2; /* behind everything */
  pointer-events: none; /* so UI stays clickable */
}

/* overlay that gives demo's dark purple base (above canvas, below content) */
.floating-overlay {
  position: fixed;
  inset: 0;
  z-index: -1; /* sits above canvas but behind content */
  pointer-events: none;
  background: linear-gradient(180deg, rgba(19,8,35,0.35) 0%, rgba(23,6,40,0.75) 45%, rgba(22,6,40,0.95) 100%);
  mix-blend-mode: normal;
  backdrop-filter: blur(0.8px);
}

/* small performance fallback for very small screens */
@media (max-width: 700px) {
  .floating-lines-container { display: none; } /* save mobile perf */
}
/* Background lines should be clickable */
.floating-lines-container {
  position: fixed;
  inset: 0;
  z-index: 1;
  pointer-events: auto !important; 
}

/* Your content must sit ABOVE visually */
.landing-root {
  position: relative;
  z-index: 5;
  pointer-events: none; /* <-- THIS IS THE FIX */
}

/* BUT all interactive elements RE-ENABLE pointer events */
nav, header, main, section, footer, button, a, .project-card, .skill-card {
  pointer-events: auto;
  position: relative;
  z-index: 10;
}

.floating-lines-container {
